import Notiflix from 'notiflix';

const errorsMessage = error => {
  if (error === 'emailExist') {
    Notiflix.Notify.info(
      '   Email  .   ',
    );
  }

  if (error === 'noCorrectPassword') {
    Notiflix.Notify.failure('   .   ');
  }

  if (error === 'noExist') {
    Notiflix.Notify.failure(
      '   Email  .   ',
    );
  }
};

export default errorsMessage;
